// 
import { Message, Notification } from 'element-ui'

const events = {
    redirect(param) {
        if (param.url) {
            window.location.href = param.url
        }
    },

    alert(param) {
        if (param.content) {
            alert(param.content)
        }
    },

    warn(param) {
        Message({ 
            message: param.content,
            type: param.type,
        })
    },

    Notification(param) {
        Notification({ 
            message: param.content,
            title: param.title,
            type: param.type,
            position: param.position, 
        })
    },
}

const mixins = {
    methods: events,
}

const eventList = [
    {
        key: 'redirect',
        label: '',
        event: events.redirect,
        param: '',
    },
    {
        key: 'alert',
        label: 'alert ',
        event: events.alert,
        param: '',
    },
    {
        key: 'warn',
        label: '',
        event: events.warn,
        param: '',
    },
    {
        key: 'Notification',
        label: '',
        event: events.Notification,
        param: '',
    },
]

export {
    mixins,
    events,
    eventList,
}