import { getStyle, getCanvasStyle } from '@/utils/style'
import { mapState } from 'vuex'
import ComponentWrapper from './ComponentWrapper'
import { changeStyleWithScale } from '@/utils/translate'
import { toPng } from 'html-to-image'
import getDom from '@/utils/getDom'

export default {
    components: { ComponentWrapper },
    model: {
        prop: 'show',
        event: 'change',
    },
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        isScreenshot: {
            type: Boolean,
            default: false,
        },
    },
    computed: mapState([
        'componentData',
        'canvasStyleData',
        'curComponent',
    ]),
    methods: {
        getStyle,
        getCanvasStyle,
        changeStyleWithScale,
        getDom,
        
        close() {
            this.$emit('change', false)
        },

        htmlToImage() {
            console.log(this.componentData)
            toPng(this.$refs.container.querySelector('.canvas'))
            .then(dataUrl => {
                const a = document.createElement('a')
                a.setAttribute('download', 'screenshot')
                a.href = dataUrl
                a.click()
            })
            .catch(error => {
                console.error('oops, something went wrong!', error)
            })
            .finally(this.close)
        },
        
        exportHTML() {
            if (this.componentData.length <= 0) return alert('')
            this.$confirm('', '', {
                type: 'warning',
                confirmButtonText: '',
                cancelButtonText: '',
            }).then(res => {
                if (res == 'confirm') {
                    const link = this.$refs.send_link
                    const url = URL.createObjectURL(new Blob([this.getDom()], { type: 'text/plain;charset="utf-8"' }))
                    link.href = url
                    link.download = 'Publish.html'
                    link.click()
                    window.URL.revokeObjectURL(url)
                }
            }).catch(err => err)
        },
    },
    
}
