export default {
    model: {
        prop: 'show',
        event: 'close',
    },
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        event: {
            type: String,
        },
    },
    data() {
        return {
            param: {
                content: '',
            },
        }
    },
    methods: {
        addEvent(event) {
            console.log(event, this.param)
            this.isShowEvent = false
            this.$emit('close')
            let param = this.param
            this.$store.commit('addEvent', { event, param })
        },

        removeEvent(event) {
            this.$store.commit('removeEvent', event)
        },
    },
}
