import { mapState } from 'vuex'

export default {
    data() {
        return {
            options: {
                color: '',
                opacity: '',
                backgroundColor: '',
                fontSize: '',
            },
        }
    },
    computed: mapState([
        'canvasStyleData',
    ]),
    methods: {
        isIncludesColor(str) {
            return str.toLowerCase().includes('color')
        },
    },
}
