export default {
    model: {
        prop: 'show',
        event: 'close',
    },
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        event: {
            type: String,
        },
    },
    data() {
        return {
            param: {
                content: '',
                type: '',
            },
            types: [{
                label: '',
                value: 'success',
            },
            {
                label: '',
                value: 'error',
            },
            {
                label: '',
                value: 'warn',
            },
            {
                label: '',
                value: 'info',
            }],
        }
    },
    methods: {
        addEvent(event) {
            this.isShowEvent = false
            this.$emit('close')
            let param = this.param
            this.$store.commit('addEvent', { event, param })
        },

        removeEvent(event) {
            this.$store.commit('removeEvent', event)
        },
    },
}
