export default {
    model: {
        prop: 'show',
        event: 'close',
    },
    props: {
        show: {
            type: Boolean,
            default: false,
        },
        event: {
            type: String,
        },
    },
    data() {
        return {
            options: [
                {
                    value: 'top-right',
                    label: '',
                },
                {
                    value: 'bottom-right',
                    label: '',
                },
                {
                    value: 'top-left',
                    label: '',
                },
                {
                    value: 'bottom-left',
                    label: '',
                },
            ],
            types: [{
                label: '',
                value: 'success',
            },
            {
                label: '',
                value: 'error',
            },
            {
                label: '',
                value: 'warn',
            },
            {
                label: '',
                value: 'info',
            }],
            param: {
                position: 'top-right',
                title: '',
                content: '',
                type: '',
            },
          
        }
    },
    methods: {
        addEvent(event) {
            this.isShowEvent = false
            this.$emit('close')
            let param = this.param
            this.$store.commit('addEvent', { event, param })
        },

        removeEvent(event) {
            this.$store.commit('removeEvent', event)
        },
    },
}
